import React from 'react';
import MaskedInput from 'react-text-mask';
import { Input, InputLabel, InputAdornment, FormControl } from '@material-ui/core';


interface TextMaskCustomProps {
  inputRef: (ref: HTMLInputElement | null) => void;
}

// coustom mask
const TextMaskCustom = (props: TextMaskCustomProps) => {
  const { inputRef, ...other } = props;

  return (
    <MaskedInput
      {...other}
      ref={(ref: any) => {
        inputRef(ref ? ref.inputElement : null);
      }}
      mask={['(', /[1-9]/, /\d/, /\d/, ')', ' ', /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/]}
      placeholderChar={'\u2000'}
      showMask
      autoFocus
      guide={false}
      placeholder='(xxx) xxx - xxxx'
    />
  );
}

interface OTPNumberInputProps {
  // pass down from OTPAuth page
  numberText: string;
  inputError: boolean;
  setNumberText: (phoneNumber: string) => void;
}

const OTPNumberInput: React.FC<OTPNumberInputProps> = (props) => {

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    props.setNumberText(e.target.value);
  }

  return (
    <>
      <FormControl>
        <InputLabel htmlFor="phone-number">Phone Number</InputLabel>
        <Input
          error={props.inputError}
          value={props.numberText}
          onChange={handleChange}
          name="textmask"
          id="phone-number-mask-input"
          inputComponent={TextMaskCustom as any}
          startAdornment={<InputAdornment position="start">+1</InputAdornment>}
          style={{ maxWidth: "160px" }}
        />
      </FormControl>
    </>
  );
}

export default OTPNumberInput;