import React from 'react';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import RemoveCircleIcon from '@material-ui/icons/RemoveCircle';
import { Card, CardHeader, Avatar, IconButton, makeStyles } from '@material-ui/core';

import { VendingItem } from '../assets/util';

// styles
const useStyles = makeStyles({
  title: {
    fontWeight: "bold",
  },
  subheader: {
    color: "#600EE6",
  },
});


interface StoreItemsProp {
  item: VendingItem;
  addToCart: (key: number) => void;
  deleteFromCart: (key: number) => void;
  itemNumber?: number
}

const StoreItems: React.FC<StoreItemsProp> = ({ item, addToCart, deleteFromCart, itemNumber }) => {
  const [number, setNumber] = React.useState<number>(0);
  // styles
  const classes = useStyles();

  React.useEffect(() => {
    if (itemNumber) {
      setNumber(itemNumber);
    }
  }, [itemNumber]);

  const addOne = () => {
    if (number + 1 <= item.maxQuantity) {
      setNumber(number + 1);
      addToCart(item.id);
    }
  }

  const deleteOne = () => {
    if (number - 1 >= 0) {
      setNumber(number - 1);
      deleteFromCart(item.id);
    }
  }

  const disableDelete: boolean = number === 0 ? true : false;
  const disableAdd: boolean = number === item.maxQuantity ? true : false

  return (
    <div>
      <Card variant="outlined" style={{ maxWidth: "320px", margin: "auto" }}>
        <CardHeader
          classes={{
            title: classes.title,
            subheader: classes.subheader
          }}
          avatar={
            <Avatar src={item.img} alt={item.name} />
          }
          title={item.name}
          subheader={`Price:$ ${item.price} Available: ${item.maxQuantity}`}
          action={
            <div>
              <IconButton size="small" onClick={deleteOne} disabled={disableDelete}>
                <RemoveCircleIcon style={{ color: "600EE6" }} />
              </IconButton>
              {number}
              <IconButton size="small" onClick={addOne} disabled={disableAdd}>
                <AddCircleIcon style={{ color: "600EE6" }} />
              </IconButton>
            </div>
          }
        />
      </Card>
    </div>
  );
}

export default StoreItems;