import API, { graphqlOperation } from '@aws-amplify/api';
import { getStore } from '../graphql/queries';

// item interface
export interface VendingItem {
  index: number;
  id: number;
  img: string;
  title: string;
  name: string;
  price: number;
  maxQuantity: number;
}

export const getStoreItem = async (givenStoreId: string) => {
  try {
    const response: any = await API.graphql(
      graphqlOperation(getStore, {
        storeId: givenStoreId,
      })
    );

    if (response.data.getStore !== null) {
      // navigation.navigate('StoreScreen2', {
      //   storeItems: JSON.parse(response.data.getStore.items),
      // });
      // await AsyncStorage.setItem('storeItemDB', response.data.getStore.items);

      // to do: with data
      return JSON.parse(response.data.getStore.items);

    } else {
      // Alert.alert("Store Doesn't Exist");
      return null;
    }
  } catch (err) {
    console.log('error finding store...', err);
    return null;
  }
};

