import React from 'react';
import { BrowserRouter as Router, Redirect, Route, Switch } from "react-router-dom";

import OTPAuth from './pages/OTPAuth';
import Checkout from './pages/Checkout';
import Store from './pages/Store';

import './App.css';
import { VendingItem } from './assets/util';

interface AppState {
  user: object,
  cart: Map<number, number>,
  storeItems: [VendingItem] | null,
  storeId: string | null,
}

class App extends React.Component<{}, AppState> {
  constructor(props: any) {
    super(props);
    // this.state = {
    //   user: {}
    // }
    // temp bypass auth
    this.state = {
      user: { bypass: true },
      cart: new Map<number, number>(),
      storeItems: null,
      storeId: null,
    }
  }

  setUser = (user: object) => {
    this.setState({ user });
  }

  setStoreItem = (items: [VendingItem]) => {
    this.setState({ storeItems: items });
  }

  setStoreId = (id: string | null) => {
    this.setState({ storeId: id });
  }

  addToCart = (itemID: number) => {
    // chcek if exist
    if (this.state.cart.has(itemID)) {
      // @ts-ignore
      this.setState({ cart: this.state.cart.set(itemID, this.state.cart.get(itemID) + 1) });
    } else {
      this.setState({ cart: this.state.cart.set(itemID, 1) });
    }
  }

  deleteFromCart = (itemID: number) => {
    // chcek if exist
    if (this.state.cart.has(itemID)) {
      // @ts-ignore
      this.setState({ cart: this.state.cart.set(itemID, this.state.cart.get(itemID) - 1) });
    }
  }

  // private route, check the auth
  PrivateRoute = ({ component: Component, data, ...rest }: any) => {
    return (
      <Route {...rest} render={(props) => (Object.keys(this.state.user).length > 0
        ? <Component {...props} {...data} />
        : <Redirect to="/" />)} />
    );
  };

  render() {
    return (
      <Router basename={process.env.PUBLIC_URL}>
        <div className="App">
          <Switch>
            <this.PrivateRoute
              component={Checkout}
              data={{
                addToCart: this.addToCart,
                deleteFromCart: this.deleteFromCart,
                items: this.state.storeItems,
                cart: this.state.cart,
                // @ts-ignore
                phoneNumber: this.state.user.username,
              }}
              path="/checkout" />
            <this.PrivateRoute
              component={Store}
              data={{
                addToCart: this.addToCart,
                deleteFromCart: this.deleteFromCart,
                items: this.state.storeItems,
                storeId: this.state.storeId,
                setStoreItems: this.setStoreItem
              }}
              path="/store" />
            <Route path="/" exact render={() => <OTPAuth setUser={this.setUser} setStoreItems={this.setStoreItem} setStoreId={this.setStoreId} />} />
          </Switch>
        </div>
      </Router>

    );
  }
}

export default App;
