import React from 'react';
import { useHistory, useLocation } from 'react-router-dom';
import { Button, TextField, CircularProgress } from '@material-ui/core';

import OTPNumberInput from '../components/OTPNumberInput';
import logo from '../assets/Travtail.png';

import Amplify from '@aws-amplify/core';
import Auth from '@aws-amplify/auth';
import awsconfig from '../aws-exports';

import '../App.css';
import { getStoreItem, VendingItem } from '../assets/util';

Amplify.configure(awsconfig);


interface OTPAuthProps {
  setUser: (user: object) => void;
  setStoreItems: (items: [VendingItem]) => void;
  setStoreId: (id: string| null) => void;
}

const useQuery = () => {
  return new URLSearchParams(useLocation().search);
}

const OTPAuth: React.FC<OTPAuthProps> = props => {
  // indcate whether user click verify button
  const [msgSent, setMsgSent] = React.useState<boolean>(false);
  // phone number
  const [numberText, setNumberText] = React.useState<string>('');
  // verification code
  const [code, setCode] = React.useState<string>('');
  // cooldown timer for resent
  const [coolDown, setCoolDown] = React.useState<number>(0);
  // check phone input error
  const [phoneError, setPhoneError] = React.useState<boolean>(false);
  // user session
  const [session, setSession] = React.useState(null);
  // message to display
  const [message, setMessage] = React.useState<string>('Welcome, please verify your phone');
  const [loading, showLoading] = React.useState<boolean>(false);
  // auto generate password
  const password = Math.random().toString(10) + 'Abc#';

  // react router
  const history = useHistory();
  // get query params
  let query = useQuery();

  // get the phone number from numberText states
  const getPhoneNumber = () => {
    return '+1' + numberText.replace(/\s|\(|\)|-/g, '');
  }


  const signIn = async () => {
    // log
    console.log(`${new Date().toISOString()}: SignIn event`);
    try {
      const result = await Auth.signIn(getPhoneNumber());
      if (result) {
        setSession(result);
      }
    } catch (error) {
      if (error.code === 'UserNotFoundException') {
        signUp();
      } else {
        console.log(error.code);
        console.log('error signing in:', error);
      }
    }
  };

  const signUp = async () => {
    console.log('new sign up');
    try {
      await Auth.signUp({
        username: getPhoneNumber(),
        password,
        attributes: {
          phone_number: getPhoneNumber(),
        },
      });

    } catch (error) {
      console.log('error signing up:', error);
    }
  };

  const verifyOtp = async () => {
    try {
      const user = await Auth.sendCustomChallengeAnswer(session, code);
      if (user) {
        props.setUser(user);
        setSession(null);
        console.log(user);
        // load the store item
        if (query.get('store')) {
          // show loading stage
          showLoading(true);
          setMessage('Loading the store');
          // pass store id
          props.setStoreId(query.get('store'));
          // @ts-ignore
          const items = await getStoreItem(query.get('store'));
          if (items) {
            props.setStoreItems(items);
          }
        }
        // push to store page
        history.push('/store');
      }
    } catch (error) {
      setMessage('Authentication fail, new code sent');
      await signIn();
      setCode('');
      console.log(error);
    }
  };

  // handler for send message button
  const onSendButtonClick = () => {
    // check the phone number
    if (getPhoneNumber().length === 12) {
      signIn();
      setMessage('Verify with SMS code');
      // reset Phone number error
      setPhoneError(false);
      // mark msg sent to show code input
      setMsgSent(true);
      // set up coolDown time
      setCoolDown(10);
    } else {
      setPhoneError(true);
      setMessage('Please input valid phone number')
    }
  }

  // get verification code input
  const onCodeInputChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setCode(event.target.value);
  }

  // set up cutdown, update cooldown time
  React.useEffect(() => {
    coolDown > 0 && setTimeout(() => setCoolDown(coolDown - 1), 1000);
  }, [coolDown]);


  // the verification code textinput, only display when send is click
  const verificationInput = msgSent ? (
    <>
      <TextField
        id="verification-code"
        label="Input code"
        size="small"
        onChange={onCodeInputChange}
        autoComplete="off"
        style={{ maxWidth: "150px" }}
      />
      <Button
        variant="contained"
        style={{ margin: "12px", backgroundColor: "#600EE6", color: "white" }}
        disabled={code ? false : true}
        onClick={verifyOtp}
      >Verify</Button>
    </>
  ) : null;

  return (
    <div className="App-header">
      <img src={logo} className="App-logo" alt="logo" />
      <div>
        <p>{message}</p>
      </div>
      <div>{loading ? <CircularProgress /> : null}</div>
      <div>
        <OTPNumberInput numberText={numberText} setNumberText={setNumberText} inputError={phoneError} />
        <Button
          onClick={onSendButtonClick}
          variant="contained"
          style={{ margin: "12px", backgroundColor: "#600EE6", color: "white" }}
          disabled={coolDown === 0 ? false : true}
        >
          {msgSent ? `Resend${coolDown ? `(${coolDown})` : ''}` : 'Send'}
        </Button>
      </div>
      <div>
        {verificationInput}
      </div>
      <div style={{ paddingTop: "20px" }}>
        {query.get('store') ? `Current store #${query.get('store')}` : 'Not a valid store'}
      </div>
    </div>
  );
}

export default OTPAuth;
