import React from 'react';
import StoreItems from '../components/StoreItems';
import { useHistory } from 'react-router-dom';
import { Button, IconButton } from '@material-ui/core';
import RefreshIcon from '@material-ui/icons/Refresh';

// temp function to mimic database API call 
import { getStoreItem, VendingItem } from '../assets/util';

interface StoreProps {
  addToCart: (key: number) => void;
  deleteFromCart: (key: number) => void;
  setStoreItems: (items: [VendingItem]) => void;
  items: [VendingItem] | null;
  storeId: string | null;
}

const Store: React.FC<StoreProps> = (props) => {

  const history = useHistory();

  const goToCheckout = () => {
    history.push("/checkout");
  }


  const getStoreData = async () => {
    if ((!props.items) && props.storeId) {
      const result = await getStoreItem(props.storeId);
      props.setStoreItems(result);
    }
  }

  const itemsList = props.items ? props.items.map((value, index) =>
    <StoreItems key={index} addToCart={props.addToCart} deleteFromCart={props.deleteFromCart} item={value} />)
    : (
      props.storeId ?
        <div style={{ textAlign: "center" }}>
          <p>Refresh to get store items</p>
          <IconButton onClick={getStoreData} style={{ color: "#600EE6" }}>
            <RefreshIcon />
          </IconButton>
        </div>
        :
        <div style={{ textAlign: "center" }}>Unable to load store items, scan again</div>
    );
  return (
    <div>
      <div style={{ position: "fixed", top: "0", width: "100%", backgroundColor: "#600EE6" }}>
        <p style={{ fontSize: "22px", marginTop: "2vh", color: "white", fontWeight: 500 }}>
          {props.storeId ? `Store #${props.storeId}` : 'Store'}
        </p>
      </div>
      <div style={{ minHeight: "92vh", marginTop: "8vh", backgroundColor: "whitesmoke" }}>
        <div style={{ paddingTop: "20px", textAlign: "left" }}>
          {itemsList}
        </div>
        <div style={{ padding: "10px" }}>
          {props.items && <Button onClick={goToCheckout} style={{ color: "#600EE6", fontWeight: "bold" }}>Show Cart</Button>}
        </div>
      </div>
    </div>
  );
}

export default Store;