import React from 'react';
import { useHistory } from 'react-router-dom';
import { Button } from '@material-ui/core';
import { loadStripe } from "@stripe/stripe-js";

import StoreItems from '../components/StoreItems';
import { VendingItem } from '../assets/util';

import { API } from 'aws-amplify';

// Make sure to call `loadStripe` outside of a componentâ€™s render
const stripePromise = loadStripe("pk_test_Wks4vayZlORiZoodc5IA0RhA00RaDpuklR");

interface CheckoutProps {
  cart: Map<number, number>;
  addToCart: (key: number) => void;
  deleteFromCart: (key: number) => void;
  items: [VendingItem] | null;
  phoneNumber: string | undefined;
}

const Checkout: React.FC<CheckoutProps> = ({ cart, addToCart, deleteFromCart, items, phoneNumber }) => {
  // react router
  const history = useHistory();
  // get checkout result
  const [message, setMessage] = React.useState<string>("");
  // setup message if this is a redirect back from checkout
  React.useEffect(() => {
    // Check to see if this is a redirect back from Checkout
    const query = new URLSearchParams(window.location.search);
    if (query.get("success")) {
      setMessage("Order placed! You will receive an email confirmation.");
    }
    if (query.get("canceled")) {
      setMessage(
        "Order canceled -- continue to shop around and checkout when you're ready."
      );
    }
  }, []);

  const checkoutItems = items ? items.filter((item) => {
    if (cart.has(item.id)) {
      // @ts-ignore
      if (cart.get(item.id) > 0) {
        return true;
      }
    }
    return false;
  }) : [];


  // handle checkout
  const handleCheckout = async () => {
    // before checkout, add numbers of purchase info
    checkoutItems.forEach((value) => {
      // @ts-ignore
      value.number = cart.get(value.id);
    });
    let myInit = {
      response: true,
      body: {
        items: checkoutItems,
        phone: phoneNumber? phoneNumber : ''
      },
      headers: {} // OPTIONAL
    }

    try {
      const response = await API.post("stripeIntegration", "/stripe", myInit);
      console.log("setupIntents", response);

      const stripe = await stripePromise;
      //const session = await response.json();
      // When the customer clicks on the button, redirect them to Checkout.
      const result = await stripe?.redirectToCheckout({
        sessionId: response.data.id,
      });
      if (result?.error) {
        // If `redirectToCheckout` fails due to a browser or network
        // error, display the localized error message to your customer
        // using `result.error.message`.
      }
    } catch (e) {
      console.log("error", e);
    }

  };

  const itemLists = items ? items.filter((item) => {
    if (cart.has(item.id)) {
      // @ts-ignore
      if (cart.get(item.id) > 0) {
        return true;
      }
    }
    return false;
  }).map((value, index) =>
    <StoreItems key={index} addToCart={addToCart} deleteFromCart={deleteFromCart} item={value} itemNumber={cart.get(value.id)} />)
    : (
      <div style={{textAlign:"center"}}>
        Unable to get store items
      </div>
    );

  return message ? (
    <div>
      <p>{message}</p>
      <button onClick={() => { history.push('/') }}>return</button>
    </div>
  ) :
    (
      <div>
        <div style={{ position: "fixed", top: "0", width: "100%", backgroundColor: "#600EE6" }}>
          <p style={{ fontSize: "22px", marginTop: "2vh", color: "white", fontWeight: 500 }}>Checkout</p>
        </div>
        <div style={{ minHeight: "92vh", marginTop: "8vh", backgroundColor: "whitesmoke" }}>
          <div style={{ paddingTop: "20px", textAlign: "left" }}>
            {itemLists}
          </div>
          <div style={{ padding: "10px" }}>
            <Button
              onClick={handleCheckout}
              style={{ borderRadius: "10px", backgroundColor: "#600EE6", color: "white" }}
            >
              Checkout
            </Button>
          </div>
        </div>
      </div>
    );
}

export default Checkout;